/*
Page Formatting Below
*/
.banner {
    display: grid;
    width: 100%;
    height: 80px;
    background-image: url('images/Icon_Resized.png'), url('images/Banner.png');
    background-repeat: no-repeat;
    background-size: contain, cover;
}
.sitehead {
    background-image: url('images/Home_Background.png');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    display: grid;
    max-width: 100%;
    height: 320px;
    grid-template-columns: [sidebar] 10% [body] 90% [bodyend];
    grid-template-rows: [navbar] 30px [heading] 80px [body1];
    grid-template-areas:
        "navbar navbar"
        "heading heading"
        ;
    margin: 0px;
}
.sitebackground {
    background-color: white;
    display: grid;
    width: 100%;
    height: 250px;
    grid-template-columns: [sidebar] 20% [body] 80% [bodyend];
    grid-template-rows: [body1] 1fr [body2] 1fr [body3] 1fr [body4];
    grid-template-areas:
        "sidebar body1"
        "sidebar body2"
        "sidebar body3"
        ;
    margin: 0px;
}
.navigationbar {
    grid-area: navbar;
    background-color: rgba(255,255,255,0.8); 
    border: 2px solid black; 
    padding: 10px;
    text-align: left;
    color: black;
}
.heading1 {
    grid-area: heading;
    background-color: rgba(255,255,255,0.8);
    color: #C41230;
    text-align: left;
    text-indent: 10%;
    padding: 10px;
    font-size: 60px;
    font-family: sans-serif;
}
.pagebody {
    grid-area: body1;
    color: darkslategray;
}
.sidebar {
    grid-area: sidebar;
    padding: 5px;
    text-align: center;
}

/*
Text formating below
*/
//Should the text be held in a box or not?
.boxedtext {
    background-color: #E0E0E0; 
    color: black;
    border: 3px solid black;
    border-radius: 25px;
    padding: 25px;
    text-align: left;
    text-indent: 0%;
}
.unboxedtext {
    padding: 10px;
    text-align: left;
}
//Within the box or not, what should the text itself look like?
.heading2 {
    color: #C41230;
    text-align: left;
    text-indent: 0%;
    font-size: 20px;
    font-family: sans-serif;
}
.heading3 {
    color: rgb(109,110,113);
    text-align: left;
    text-indent: 1%;
    font-size: 15px;
    font-family: sans-serif;
}

//slightly indented text
.listsnippet {
    color: black;
    text-align: left;
    text-indent: 2%;
    font-size: 15px;
    font-family: sans-serif;
}


.navigationtext {
    color: #C41230;
    text-decoration: none;
    font-size: 15px;
    font-family: sans-serif;
    font-weight: bold;
    margin: 10px;
    padding: 2px;
}
//sidebar formatting
.sidebartext {
    color: #C41230;
    text-decoration: none;
    font-size: 15px;
    font-family: sans-serif;
    font-weight: bold;
    margin: 10px;
    padding: 2px;
}
hr.sidebarlines {
    border-color: #C41230;
}
// Link formats
a:hover {
    color: rgb(149,17,32);
}

